import { Component, OnInit, Input, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivitySearchService } from './../../../../shared/services/activity.service';
import { Router, ActivatedRoute } from '@angular/router';
import { CommonService } from 'src/app/shared/services/common.service';
import { ToastrService } from 'ngx-toastr';
import { environment } from 'src/environments/environment';

declare const moment: any;
declare const $: any;


@Component({
  selector: 'app-block',
  templateUrl: './block.component.html',
  styleUrls: ['./block.component.scss']
})
export class BlockComponent implements OnInit {

  checkoutForm:FormGroup;
  destinations:string[]=[];
  activityBook: any;
  isSearchCompleted: boolean = false;
  @Input() activitySearchCriteria:any;
  activityblockList: any;
  single:any;
  countries: any;
  constructor(
    private fb:FormBuilder,
    private ActivitySearchService: ActivitySearchService,
    private router: Router,
    private activatedRoute:ActivatedRoute,
    private commonService: CommonService,
    private toastr: ToastrService,
    ) 
    {
      console.log(this.router.getCurrentNavigation().extras.state);
     }
     ngOnInit() {
      this.initForm();
      //console.log('insuranceSearchCriteria',this.insuranceSearchCriteria);
     
      this.activityblockList=history.state;
      
      this.single=this.activityblockList.result.ActivityResult.Activity;
      console.log(this.single);
    }
    ngAfterViewInit() {
      this.loadConfig();
  
    }
    initForm(){
      this.checkoutForm=this.fb.group({
        
  
        studentPlan:[''],
        agentReference:[''],
        arrivalairportcode:[''],
        bookingAmount:[''],
        bookingCode:[''],
        bookingComments:[''],
        bookingReference:['fgdfgdfgd3454'],
        bookingDate:[''],
        bookingCurrency:[''],
        bookingStatus:[''],
        departureairportcode:[''],
        tripID:['TSS50972D2024S'],
        booking_status:[''],
        is_blocked:[''],
  
        city:[''],
        nationality:[''],
        residence:[''],
        street1:[''],
        beneficiary:[''],
        birth_date:[''],
        cnic:[''],
        email:[''],
        first_name:[''],
        gender:[''],
        last_name:[''],
        passenger_type:[''],
        passport:[''],
        relativeCity:[''],
        relativeName:[''],
        relativePhone:['']
        
       
      });
      this.getCountries();
      this.departureDate();
    }
  
  
  
    bookactivity() {
  
      // console.log('formmmmmmm',this.checkoutForm.value);
      // console.log("Insurance list........",this.insuranceblockList.result.insurances.Insurance);
      // console.log("InsuranceSearchCriteria........",this.insuranceblockList.result.insurances.InsuranceSearchCriteria);
      
      const datas = {
        cl: this.checkoutForm.value,
        il: this.activityblockList.result.ActivityResult.Activity,
        isc:this.activityblockList.result.ActivityResult.ActivitySearchCriteria
       
      }
      console.log("test....", datas);
      this.ActivitySearchService.orderCreateRequest(datas).subscribe((resp: any) => {
        if (resp.status) {
          this.isSearchCompleted = true;
       
          this.activityBook = resp['result'].Acitvity;
          this.router.navigate(['bookings']);
          this.activitySearchCriteria = resp['result'].Acitvity.ActivitySearchCriteria;
          console.log('booked plan-----------',this.activityBook);
          this.toastr.success(`${resp.message}`, ``, {
            timeOut: 4000,
            progressBar: false,
            positionClass: 'toast-bottom-right',
            
          });
  
  
            // console.log('server responsesssssssssssssssss....',resp);
  
          //  console.log('insuranceSearchCriteria response....',this.insuranceSearchCriteria);
         
          // resp.result.insurances.Insurances.Insurance.map(ele => {
       
          //   ele.insuraceSearchCriteria=resp.result.insurances.InsuranceSearchCriteria;
          //   this.insuranceList = resp.result.insurances.Insurances.Insurance;
          //  // console.log("----------", resp.result.insurances.Insurances.Insurance)
          // }
          // )
        }else{
          this.toastr.error(`${resp.message}`, ``, {
            timeOut: 1000,
            progressBar: false,
            positionClass: 'toast-bottom-right'
          });
         }
        
      });
     
    }
  
  
    loadConfig() {
      var scope = this;
  
      $(document).ready(function () {
        $('.ui.dropdown').dropdown();
        $('.dropdown4').dropdown({ placeholder: "Choose origin airports",});
   
        this._globals=localStorage.getItem('_globals');
        let token=JSON.parse(this._globals).token;
        // console.log(token);
        $('.ui.dropdown4').dropdown({
          type: 'category',
          minCharacters : 2,
          maxCharacters : 3,
          responseAsync: true,
          apiSettings: {
            url: environment.serverURL + '/api/insurance/get-airports/{query}',
            beforeXHR: function (xhr) {
              xhr.setRequestHeader('Authorization',token);
              return xhr;
            },
            onResponse: function (response) {
              const result = { success: true, results: [] };
              $.each(response.result, function (index, item) {
  
                result['results'].push({
                  name: item.AirportName, 
                  value: item.AirportCode
                })
              });
  
              return result;
            },
          },
        })
      });
    }
  
    getCountries() {
      this.commonService.getCountryList().subscribe((result: any) => {
        //console.log(result)
        this.countries = result.countries;
      });
    }
    
    
      
    
   
  
    departureDate() {
      var scope = this;
      $(function () {
        $('input[name="birth_date"]').daterangepicker({
          singleDatePicker: true,
          showDropdowns: true,
          autoUpdateInput: false,
        }, function (start, end, label) {
          var years = moment().diff(start, 'years');
          scope.checkoutForm.patchValue({'birth_date': moment(start).format('YYYY-MM-DD')});
        });
      });
    }
  
  
  
}





  
      
 
